/* News Page Specific Styles */

/*  */
.news-filter-section {
  background: #f8f9fa;
  padding: 30px 0;
  border-bottom: 1px solid #e9ecef;
}

.news-filter {
  display: flex;
  justify-content: center;
  gap: 15px;
  flex-wrap: wrap;
}

.filter-btn {
  padding: 10px 30px;
  border: 2px solid var(--primary-color);
  background: white;
  color: var(--text-primary);
  font-size: 1rem;
  font-weight: 600;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.filter-btn:hover {
  background: var(--primary-color);
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(139, 199, 128, 0.3);
}

.filter-btn.active {
  background: var(--primary-color);
  color: white;
  box-shadow: 0 4px 12px rgba(139, 199, 128, 0.3);
}

/*  */
.news-list-section {
  padding: 80px 0;
  background: white;
}

.news-grid {
  display: flex;
  flex-direction: column;
  gap: 0;
}

/*  */
.news-card {
  background: white;
  padding: 30px 0;
  border-bottom: 2px solid #f0f0f0;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.news-card:hover {
  background: #f8f9fa;
}

/*  -  */
.news-card-with-image {
  padding: 40px 0;
  background: linear-gradient(to bottom, #ffffff 0%, #f8f9fa 100%);
  border-bottom: 3px solid #e0e0e0;
}

.news-card-with-image:hover {
  background: linear-gradient(to bottom, #f8f9fa 0%, #f0f0f0 100%);
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
}

.news-card-with-image .news-card-title {
  font-size: 1.6rem;
  color: var(--text-primary);
}

/*  -  */
.news-card-text-only {
  padding: 25px 0;
}

.news-card-text-only .news-card-title {
  font-size: 1.3rem;
}

.news-card-header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 20px;
  flex-wrap: wrap;
  gap: 15px;
}

.news-card-date {
  font-size: 0.95rem;
  color: #666;
  font-weight: 600;
}

.news-card-labels {
  display: flex;
  align-items: center;
  gap: 10px;
}

.news-card-image {
  max-width: 800px;
  width: 100%;
  margin: 0 auto 20px;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.news-card-image img {
  width: 100%;
  height: auto;
  max-height: 500px;
  display: block;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.news-card:hover .news-card-image img {
  transform: scale(1.02);
}

/*  */
.news-card-images {
  max-width: 800px;
  width: 100%;
  margin: 0 auto 20px;
  position: relative;
}

.news-images-slider {
  position: relative;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.news-images-track {
  display: flex;
  transition: transform 0.3s ease;
}

.news-image-slide {
  min-width: 100%;
  height: auto;
}

.news-image-slide img {
  width: 100%;
  height: auto;
  max-height: 500px;
  display: block;
  object-fit: cover;
}

/*  */
.news-images-nav {
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 8px;
  z-index: 10;
}

.news-images-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
  border: 2px solid rgba(255, 255, 255, 0.8);
  cursor: pointer;
  transition: all 0.3s ease;
}

.news-images-dot.active {
  background: white;
  width: 12px;
  height: 12px;
}

/*  */
.news-images-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(255, 255, 255, 0.9);
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(0,0,0,0.2);
}

.news-images-arrow:hover {
  background: white;
  transform: translateY(-50%) scale(1.1);
}

.news-images-arrow-prev {
  left: 15px;
}

.news-images-arrow-next {
  right: 15px;
}

/*  */
.news-images-counter {
  position: absolute;
  top: 15px;
  right: 15px;
  background: rgba(0, 0, 0, 0.6);
  color: white;
  padding: 5px 12px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  z-index: 10;
}

.news-card-category {
  padding: 5px 15px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  color: white;
}

.category- {
  background: linear-gradient(135deg, #FF6B6B, #FF8E8E);
}

.category- {
  background: linear-gradient(135deg, #4ECDC4, #6FE5DC);
}

.category- {
  background: linear-gradient(135deg, #FFD93D, #FFE66D);
  color: #333 !important;
}

.news-card-badge {
  background: linear-gradient(135deg, #FF6B6B, #FF8E8E);
  color: white;
  padding: 5px 12px;
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 700;
  letter-spacing: 0.5px;
  animation: pulse-badge 2s ease-in-out infinite;
}

@keyframes pulse-badge {
  0%, 100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(255, 107, 107, 0.4);
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 0 0 6px rgba(255, 107, 107, 0);
  }
}

.news-card-title {
  font-size: 1.4rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 15px;
  line-height: 1.5;
}

.news-card-description {
  font-size: 1rem;
  color: #666;
  line-height: 1.7;
  margin-bottom: 12px;
}

.news-card-content {
  font-size: 0.95rem;
  color: #888;
  line-height: 1.7;
  margin-bottom: 20px;
}

.news-card-link {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  color: var(--primary-color);
  font-weight: 600;
  font-size: 0.95rem;
  text-decoration: none;
  transition: all 0.3s ease;
  margin-top: 15px;
}

.news-card-link:hover {
  gap: 12px;
  color: var(--primary-dark);
}

.news-card-link svg {
  transition: transform 0.3s ease;
}

.news-card-link:hover svg {
  transform: translateX(4px);
}

/*  */
.news-empty {
  text-align: center;
  padding: 80px 20px;
}

.news-empty p {
  font-size: 1.2rem;
  color: #999;
}

/*  */
@media (max-width: 768px) {
  .news-filter-section {
    padding: 20px 0;
  }

  .news-filter {
    gap: 10px;
  }

  .filter-btn {
    padding: 8px 20px;
    font-size: 0.9rem;
  }

  .news-list-section {
    padding: 50px 0;
  }

  .news-card {
    padding: 25px 0;
  }

  .news-card-title {
    font-size: 1.2rem;
  }

  .news-card-description,
  .news-card-content {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .news-card {
    padding: 20px 0;
  }

  .news-card-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .news-card-title {
    font-size: 1.1rem;
  }

  .filter-btn {
    padding: 8px 18px;
    font-size: 0.85rem;
  }
}

/*  */
.news-loading {
  text-align: center;
  padding: 100px 20px;
}

.news-spinner {
  width: 50px;
  height: 50px;
  margin: 0 auto 20px;
  border: 4px solid #f0f0f0;
  border-top: 4px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.news-loading p {
  font-size: 1.1rem;
  color: #666;
  font-weight: 500;
}

/*  */
.news-error {
  text-align: center;
  padding: 100px 20px;
  background: #fff3cd;
  border-radius: 12px;
  margin: 40px 0;
}

.news-error p {
  font-size: 1.1rem;
  color: #856404;
  margin-bottom: 20px;
  font-weight: 500;
}

.news-reload-btn {
  padding: 12px 30px;
  background: var(--primary-color);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.news-reload-btn:hover {
  background: var(--primary-dark);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(139, 199, 128, 0.3);
}
